<!-- 登陆页面 -->
<template>
  <div class="login">
    <div class="login-logo">
      <router-link to="/" tag="span"></router-link>
    </div>
    <div class="login-main">
      <div class="main-box">
        <h1>Sign-In</h1>
        <div class="input">
          <p>Email (phone for mobile accounts)</p>
          <input type="text" v-model="username" />
        </div>
        <div class="input">
          <p>Password</p>
          <input type="password" v-model="password" />
        </div>
        <div class="submit">
          <button @click="loginFn">Sign-In</button>
        </div>
      </div>
      <div class="account">
        <h5>New to Amazon?</h5>
      </div>
      <div class="l-register" @click="toregisterFn">Create your Amazon account</div>
    </div>
    <div class="login-footer"></div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
import Axios from "axios";
import qs from "qs"; //将数据序列化

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      username: null,
      password: null
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    toregisterFn() {
      //跳转注册页面
      this.$router.push({
        path: "/register"
      });
    },
    loginFn() {
      //点击登陆
      if (this.username != null && this.password != null) {
        Axios.post(
          this.$store.state.url + "uLogin",
          qs.stringify({
            username: this.username,
            password: this.password
          })
        ).then(res => {
          console.log(res);
          if (res.data.code == 200) {
            this.$message({
              message: "登陆成功",
              type: "success"
            });
            this.$cookies.set("username", res.data.data.realname, "0");
            this.$router.push("/");
            // this.$store.commit("userFn", res.data.data.realname);
          } else if (res.data.code == 400) {
            this.$message.error("账号或密码错误");
          }
        });
      } else {
        this.$message({
          message: "请将账号密码输入完整",
          type: "warning"
        });
      }
    }
  },
  mounted() {
    console.log(this.$cookies);
  }
};
</script>
<style lang='less'>
//@import url(); 引入公共css类
.login {
  padding: 14px 18px;
  .login-logo {
    text-align: center;
    margin-bottom: 18px;
    span {
      display: inline-block;
      width: 103px;
      height: 31px;
      background: url("../assets/images/jingling2.png") no-repeat -5px -130px;
      background-size: 400px 750px;
      cursor: pointer;
    }
  }
  .login-main {
    width: 700px;
    margin: 10px auto 35px;
    .main-box {
      margin: 0 auto 22px;
      width: 298px;
      padding: 20px 26px;
      height: 240px;
      border-radius: 4px;
      border: 1px solid #ccc;
      h1 {
        font-weight: 400;
        font-size: 28px;
        line-height: 1.2;
        margin-bottom: 10px;
      }
      .input {
        margin-bottom: 10px;
        p {
          padding-left: 2px;
          padding-bottom: 2px;
          font-weight: 700;
          font-size: 13px;
          line-height: 19px;
          color: #111;
          font-family: "Hiragino Sans GB", "Microsoft Yahei", Arial, sans-serif;
        }
        input {
          width: 100%;
          background-color: #fff;
          height: 31px;
          padding: 3px 7px;
          box-sizing: border-box;
          line-height: normal;
          border: 1px solid #a6a6a6;
          border-radius: 3px;
          outline-color: #f90;
        }
      }
      .submit {
        padding-top: 10px;
        button {
          width: 100%;
          height: 30px;
          outline: none;
          line-height: 30px;
          border: 1px solid;
          cursor: pointer;
          background: #f3d078;
          border-color: #a88734 #9c7e31 #846a29;
          text-align: center;
          font-size: 13px;
          color: #111;
          &:hover {
            background: #f0c14b;
          }
        }
      }
    }
    .account {
      width: 350px;
      margin: 0 auto;
      text-align: center;
      position: relative;
      margin-bottom: 14px;
      h5 {
        font-size: 12px;
        color: #767676;
        font-weight: 400;
        z-index: 2;
        position: relative;
        display: inline-block;
        background-color: #fff;
        padding: 0 8px 0 7px;
      }
      &::after {
        content: "";
        width: 100%;
        background-color: transparent;
        display: block;
        height: 1px;
        border-top: 1px solid #e7e7e7;
        position: absolute;
        top: 50%;
        margin-top: -1px;
        z-index: 1;
      }
    }
    .l-register {
      font-size: 13px;
      line-height: 29px;
      color: #111;
      width: 350px;
      height: 29px;
      margin: 0 auto;
      text-align: center;
      background: #e7e9ec;
      border-radius: 3px;
      border: 1px solid;
      border-color: #adb1b8 #a2a6ac #8d9096;
      cursor: pointer;
      &:hover {
        background: #d9dce1;
      }
    }
  }
  .login-footer {
    position: relative;
    text-align: center;
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.14),
      rgba(0, 0, 0, 0.03) 3px,
      transparent
    );
    &::after {
      display: block;
      width: 100%;
      height: 50px;
      line-height: 50px;
      font-size: 11px;
      color: #555;
      background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0), #fff);
      z-index: 1;
      content: "Copyright 2020 jinghuiyue company";
    }
    // .footer-t {
    //   position: absolute;
    // }
  }
}
</style>