<!-- 分类页面搜索页面 -->
<template>
  <div class="search">
    <Header ref="headers" />
    <div class="search-main">
      <div class="search-left">
        <div class="c-title">
          <span>Commodity classification</span>
        </div>
        <!-- 搜索所有过来 -->
        <div v-if="allleftsearch.length>0">
          <ul class="u-list" v-for="list in allleftsearch" :key="list.id">
            <li>{{list.typeName}}</li>
            <li
              v-for="item in list.navigation_list"
              :key="item.id"
              @click="clickproductFn(1,item.id)"
            >{{item.typeName}}</li>
          </ul>
        </div>
        <!-- 搜索一级过来 -->
        <div v-else>
          <ul class="u-list">
            <li>{{leftsearch.typeName}}</li>
            <li
              v-for="item in leftsearch.navigation_list"
              :key="item.id"
              @click="clickproductFn(1,item.id)"
            >{{item.typeName}}</li>
          </ul>
        </div>
      </div>
      <div class="search-right">
        <p
          class="p-title"
          v-if="productList.length>0"
        >Prices and other details may vary depending on size and color</p>
        <div v-if="productList.length>0">
          <ul class="right-list" :style="{'min-height':ProductHeight+'px'}">
            <li v-for="item in productList" :key="item.id">
              <router-link :to="'/detail?id='+item.id">
                <div>
                  <img :src="item.goodsMainImgPath" alt />
                </div>
                <p>{{item.goodsName}}</p>
              </router-link>
            </li>
          </ul>
          <div class="pagination">
            <el-pagination
              v-if="total>24"
              background
              layout="prev, pager, next"
              :total="total"
              :page-size="24"
              :current-page="current"
              @current-change="currentFn"
            ></el-pagination>
          </div>
        </div>
        <div v-else class="noProduct" :style="{'min-height':ProductHeight+'px'}">暂无数据</div>
      </div>
    </div>
    <Footer ref="footers" />
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import Header from "../components/Header";
import Footer from "../components/footer";
import $ from "jquery";
import Axios from "axios";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    Header,
    Footer
  },
  data() {
    //这里存放数据
    return {
      productList: [], //产品列表
      total: 0, //产品总数
      current: 1, //当前页数
      ProductHeight: 0, //高度
      leftsearch: {}, //二级导航数据
      allleftsearch: [], //点击所有分类搜索的列表
      cid: null //左边的分类id,点击分页时传入
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    $route(to) {
      this.current = 1; //数据改变时，页数返回1
      this.getproductFn(this.$route.query.id, this.$route.query.name, 1);
      if (to.query.flag == 3) {
        //搜索全部过来
        this.getAllLeftCateFn(to.query.flag, to.query.name);
      } else {
        this.getLeftCateFn(to.query.flag, to.query.id);
      }
    },
    total() {
      this.current = 1; //请求其他数据时，默认改为1
    }
  },
  //方法集合
  methods: {
    getproductFn(id, name, current) {
      //搜索跳转获取数据
      let url;
      if (!id) {
        url = `${this.$store.state.url}sys/goods/goodsList?goodsName=${name}&current=${current}&size=24`;
      } else {
        url = `${this.$store.state.url}sys/goods/goodsList?goodsType=${id}&goodsName=${name}&current=${current}&size=24`;
      }
      this.cid = null; //清空cid用于分页判断
      //请求产品数据
      Axios.get(url).then(res => {
        console.log(res);
        if (res.data.code == 200) {
          this.total = res.data.total;
          this.productList = res.data.data;
          this.ProductHeight =
            window.innerHeight -
            this.$refs.headers.$el.offsetHeight - //减去header高度
            this.$refs.footers.$el.offsetHeight - //减去footer高度
            65; //减去内边距
        }
      });
    },
    clickproductFn(current, id) {
      console.log(current, id);
      //点击左边分类得到数据
      Axios.get(
        this.$store.state.url +
          `sys/goods/navigation/goods?current=${current}&size=24&id=${id}`
      ).then(res => {
        console.log(res);
        if (res.data.code == 200) {
          this.cid = id; //左边的分类id,点击分页时传入
          this.total = res.data.total;
          this.productList = res.data.data;
          this.ProductHeight =
            window.innerHeight -
            this.$refs.headers.$el.offsetHeight - //减去header高度
            this.$refs.footers.$el.offsetHeight - //减去footer高度
            65; //减去内边距
        }
      });
    },
    getLeftCateFn(flag, id) {
      //搜索一级过来，获取数据
      Axios.get(
        this.$store.state.url + `sys/goods/navigation?flag=${flag}&id=${id}`
      ).then(res => {
        //console.log(res);
        if (res.data.code == 200) {
          this.allleftsearch = [];
          this.leftsearch = res.data.data.navigation;
        }
      });
    },
    getAllLeftCateFn(flag, name) {
      //搜索全部过来
      Axios.get(
        this.$store.state.url + `sys/goods/navigation?flag=${flag}&name=${name}`
      ).then(res => {
        //console.log(res);
        if (res.data.code == 200) {
          this.allleftsearch = res.data.data.navigation;
        }
      });
    },
    currentFn(current) {
      console.log(current);
      this.current = current;
      if (this.cid) {
        this.clickproductFn(current, this.cid);
      } else {
        this.getproductFn(
          this.$route.query.id,
          this.$route.query.name,
          current
        );
      }
    }
  },
  mounted() {
    $("#app").height("auto");
    this.getproductFn(this.$route.query.id, this.$route.query.name, 1);
    if (this.$route.query.flag == 3) {
      //搜索全部过来
      this.getAllLeftCateFn(this.$route.query.flag, this.$route.query.name);
    } else {
      this.getLeftCateFn(this.$route.query.flag, this.$route.query.id);
    }
  }
};
</script>
<style lang='less'>
.search-main {
  width: 1500px;
  height: auto;
  margin: 0 auto;
  display: flex;
  padding-top: 15px;
  .search-left {
    border-right: 2px solid #ddd;
    width: 272px;
    margin-right: 12px;
    .c-title {
      margin-bottom: 10px;
      font-size: 13px;
      line-height: 15px;
      color: #111;
      font-weight: bold;
    }
    .u-list {
      font-size: 13px;
      li:first-child {
        margin-left: 14px;
        font-weight: bold;
        &:hover {
          color: #111;
        }
      }
      li {
        margin: 0px 0px 6px 24px;
        cursor: pointer;
        &:hover {
          color: #c45500;
        }
      }
    }
  }
  .search-right {
    flex: 1;
    .p-title {
      height: 50px;
      font-size: 13px;
      line-height: 50px;
      padding-left: 12px;
    }
    .right-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        width: 276px;
        padding: 0 12px;
        a {
          display: block;
          border-bottom: 2px solid #ddd;
          margin-bottom: 26px;
          height: 410px;
          div {
            height: 275px;
            display: flex;
            align-items: center;
            justify-content: center;
            img {
              max-width: 275px;
              max-height: 275px;
            }
          }
          p {
            font-size: 15px;
            line-height: 22px;
            height: 88px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
            color: #111;
            margin-top: 10px;
            &:hover {
              color: #c45500;
            }
          }
        }
      }
    }
    .pagination {
      text-align: center;
      line-height: 60px;
    }
    .right-list::after {
      content: "";
      flex: auto;
    }
    .noProduct {
      font-size: 30px;
      color: #ccc;
      text-align: center;
      padding-top: 50px;
      height: auto;
    }
  }
}
</style>