<!-- 详情页面 -->
<template>
  <div class="detail">
    <Header />
    <div class="detail-main" id="detail-main">
      <div class="altImages">
        <ul>
          <li
            v-for="(item,index) in detailImgList"
            :key="item.id"
            @mousemove="imgmoveFn(index)"
            :class="active==index?'active':''"
          >
            <img :src="item.filePath" alt />
          </li>
        </ul>
        <!-- <div>
          <video src=""></video>
        </div>-->
      </div>
      <div class="image">
        <div class="left-image" id="left-image">
          <img :src="imagePath" alt />
          <div class="slide_box" id="box"></div>
        </div>
        <div class="rightcon" id="right">
          <img :src="imagePath" alt />
        </div>
      </div>
      <div class="detail-text">
        <h2>{{detail.goodsName}}</h2>
        <div class="detail-table">
          <table>
            <tr>
              <td>亚马逊图片要求：主图</td>
            </tr>
            <tr>
              <td>1.背景一定要纯白色，纯白的RGB值是255*255*255，但卖家不能抠图和P图。小部分home装饰用品不强制一定要用纯白背景，如床上四件套、蚊帐、窗帘、沙发、墙挂画、灯，这些可用非纯白背景的情景图等。</td>
            </tr>
            <tr>
              <td>2.是实际图，不能是绘图或者插图，甚至是漫画图。</td>
            </tr>
            <tr>
              <td>3.去除不在产品内的配件、道具等，产品最好是占据图片大约85%左右的空间，避免带有logo和水印，产品本身的logo除外。</td>
            </tr>
            <tr>
              <td>4.如果有模特，必须为真人模特，且必须是正面站立的，不能是侧面、背面、多角度组合图、坐姿等，模特身上不能有非售物品，且不包含裸体信息。</td>
            </tr>
            <tr>
              <td>5.袜子必须是单只鞋子的照片，最好是左脚朝左，穿在模特脚上的图片只能出现在附图，不能出现在主图上。</td>
            </tr>
            <tr>
              <td>6.耳环要成对出现。袜子单双卖需放单双主图。</td>
            </tr>
            <tr>
              <td>亚马逊图片要求：辅图</td>
            </tr>
            <tr>
              <td>1.避免带logo和水印，产品本身的logo除外</td>
            </tr>
            <tr>
              <td>2.辅图应该对产品做一个不同侧面的展示，使用的展示，或对在主图中没凸显的产品特性做补充</td>
            </tr>
            <tr>
              <td>3.建议纯白的背景，但这不做强制要求</td>
            </tr>
            <tr>
              <td>4.商品必须在图片中清晰可见</td>
            </tr>
            <tr>
              <td>5.如果有模特，不能是坐姿，最好站立，且要使用真人模特，不包含裸体信息</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
    <div class="product-image" v-html="detail.goodsDetail"></div>
    <Footer />
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
import Header from "../components/Header";
import Footer from "../components/footer";
import picture from "../assets/js/image.js";
import Axios from "axios";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    Header,
    Footer
  },
  data() {
    //这里存放数据
    return {
      active: 0, //判断小图片显示边框
      imagePath: "",
      detail: {},
      detailImgList: []
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    imgmoveFn(index) {
      //小图片移入
      this.active = index;
      this.imagePath = this.detailImgList[index].filePath;
    },
    getDetail(id) {
      //获取商品详情
      Axios.get(`${this.$store.state.url}sys/goods/details?id=${id}`).then(
        res => {
          //console.log(res.data);
          this.detail = res.data.data.goods; //详情
          this.detailImgList = res.data.data.img_list; //图片数据
          this.imagePath = res.data.data.img_list[0].filePath; //第一张图片作为放大图片路径
          picture(); //图片放大函数
        }
      );
    }
  },
  mounted() {
    this.getDetail(this.$route.query.id);
  }
};
</script>
<style lang='less'>
.detail-main {
  width: 1464px;
  margin: 0 auto;
  margin-top: 20px;
  display: flex;
  position: relative;
  .altImages {
    width: 40px;
    ul {
      margin-top: 18px;
      li {
        height: 50px;
        border: 1px solid #adb1b8;
        margin-bottom: 10px;
        cursor: pointer;
        img {
          width: 38px;
          height: 50px;
        }
      }
      li.active {
        border-color: #e77600;
        box-shadow: 0 0 3px 2px rgba(228, 121, 17, 0.5);
      }
    }
  }
  .image {
    width: 685px;
    height: 700px;
    margin-left: 10px;
    text-align: center;
    .left-image {
      display: inline-block;
      position: relative;
      img {
        max-height: 700px;
        max-width: 685px;
        width: auto;
      }
      .slide_box {
        // width: 100%;
        // max-width: 420px;
        width: 360px;
        height: 350px;
        background: rgba(0, 0, 0, 0.3);
        position: absolute;
        top: 0;
        left: 0;
        cursor: pointer;
        display: none;
      }
    }
    .rightcon {
      display: none;
      width: 720px;
      height: 700px;
      background: #fff;
      box-shadow: rgba(0, 0, 0, 0.25) 0px 6px 8px 2px;
      border: 1px solid rgb(148, 148, 148);
      position: absolute;
      top: 0;
      right: 0;
      overflow: hidden;
      text-align: center;
      img {
        display: inline-block;
        position: relative;
        left: 0;
        top: 0;
        width: auto;
        height: 200%;
      }
    }
  }
  .detail-text {
    flex: 1;
    margin-left: 20px;
    .detail-table {
      margin-top: 10px;
      table {
        width: 100%;
        border-collapse: collapse;
        td {
          width: 100%;
          border: 1px solid #ccc;
          padding: 10px 8px;
          font-size: 14px;
          color: #333;
        }
      }
    }
  }
}
.product-image {
  // width: 1464px;
  // margin: 0 auto;
  padding: 0 50px;
  margin-top: 10px;
  border-top: 1px solid #ccc;
  table {
    margin: 0 auto;
  }
  div {
    margin: 0 auto;
  }
  p {
    text-align: center;
    img {
      display: block;
      margin: 0 auto;
    }
  }
}
</style>