<!-- 首页main组件 -->
<template>
  <div class="main">
    <div
      class="m-section"
    >You are on amazon.com. You can also shop on Amazon China for millions of products with fast local delivery.</div>
    <div class="m-layout">
      <ul class="l-wrap">
        <router-link
          tag="li"
          class="l-basic"
          v-for="item in advertImg"
          :key="item.id"
          :to="'/detail?id='+item.id"
        >
          <!-- <div class="l-title">
            <h2>AmazonBasics</h2>
          </div>-->
          <div class="l-image">
            <img :src="item.path" alt />
          </div>
          <!-- <div class="l-footer">
            <a href>查看更多</a>
          </div>-->
        </router-link>
      </ul>
      <div class="item-list">
        <div class="item" @mouseover="mouseoverFn" @mouseleave="mouseleaveFn">
          <div class="i-title">
            <span>Best Sellers in Beauty {{"&"}} Personal Care</span>
            <a href>See more</a>
          </div>
          <div class="i-body">
            <ul class="i-list">
              <li v-for="item in productList" :key="item.id">
                <router-link :to="'/detail?id='+item.id">
                  <img :src="item.imagrPath" alt />
                </router-link>
              </li>
            </ul>
          </div>
          <div class="prevs" @click="scrollprevsFn">
            <i class="iconfont icon-zuojiantou1"></i>
          </div>
          <div class="nexts" @click="scrollnextsFn">
            <i class="iconfont icon-youjiantou1"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）

import product from "../../assets/js/product.js";
import $ from "jquery";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      advertImg: [
        {
          id: 1,
          path: require("@/assets/images/product1.png")
        },
        {
          id: 2,
          path: require("@/assets/images/product2.png")
        },
        {
          id: 3,
          path: require("@/assets/images/product3.png")
        },
        {
          id: 4,
          path: require("@/assets/images/product4.png")
        }
      ],
      productList: product.product,
      arrent: 0, //滚动的判断次数
      num: 5, //滚动的个数
      oWidth: 0 //滚动的距离
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    //滚动方法
    scrollprevsFn() {
      //向左滚动
      this.arrent--;
      if (this.arrent >= 0) {
        this.oWidth = 0;
        for (var i = 0; i < this.num * this.arrent; i++) {
          this.oWidth += $(".i-list li")
            .eq(i)
            .outerWidth(true);
        }
      } else {
        this.arrent = 0;
      }
      $(".i-list").animate({
        left: -this.oWidth
      });
    },
    scrollnextsFn() {
      //向右滚动
      this.arrent++;
      if (this.arrent < Math.ceil($(".i-list li").length / this.num)) {
        this.oWidth = 0;
        for (var i = 0; i < this.num * this.arrent; i++) {
          this.oWidth += $(".i-list li")
            .eq(i)
            .outerWidth(true);
        }
      } else {
        this.arrent = Math.ceil($(".i-list li").length / this.num) - 1;
      }
      var oWidths = null; //获取本身宽度
      for (let i = 0; i < $(".i-list li").length; i++) {
        oWidths += $(".i-list li")
          .eq(i)
          .outerWidth(true);
      }
      if (
        this.oWidth >
        oWidths -
          $(".i-list")
            .eq(0)
            .outerWidth(true)
      ) {
        // oWidths -$(".i-list").eq(0).outerWidth(true) 最大平移距离
        this.oWidth =
          oWidths -
          $(".i-list")
            .eq(0)
            .outerWidth(true);
      }
      $(".i-list").animate({
        left: -this.oWidth
      });
    },
    mouseoverFn() {
      $(".prevs,.nexts").css({
        display: "block"
      });
    },
    mouseleaveFn() {
      $(".prevs,.nexts").css({
        display: "none"
      });
    }
  },
  mounted() {}
};
</script>
<style lang='less'>
.main {
  background: rgba(c, c, c, 0.5);
  width: 1500px;
  margin: 0 auto;
  position: relative;
  z-index: 9;
  .m-section {
    width: 1460px;
    margin: 0 auto;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    text-align: center;
    color: #111;
    background: #fff;
  }
  .m-layout {
    width: 1460px;
    margin: 0 auto;
    padding: 20px 0;
    .l-wrap {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
      li {
        cursor: pointer;
        width: 310px;
        background: #fff;
        height: 345px;
        padding: 30px 20px 0;
        .l-title {
          margin-bottom: 10px;
          h2 {
            font-size: 21px;
          }
        }
        .l-footer {
          a {
            font-size: 13px;
            text-decoration: none;
            color: #0066c0;
          }
        }
        .l-image {
          height: 308px;
          margin-bottom: 20px;
          text-align: center;
          img {
            display: inline-block;
            width: 304px;
            height: 304px;
          }
        }
      }
      .l-login {
        .btn {
          margin-top: 10px;
          button {
            width: 310px;
            height: 30px;
            outline: 0;
            border: 1px solid;
            cursor: pointer;
            background: -webkit-linear-gradient(top, #f8e3ad, #eeba37);
            border-color: #c89411 #b0820f #99710d;
            &:hover {
              background: -webkit-linear-gradient(top, #eeba37, #d69f12);
            }
          }
        }
      }
      .l-category {
        .l-image {
          div {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            a {
              display: inline-block;
              width: 49.5%;
              height: 130px;
              background-color: rgba(0, 164, 180, 0.05);
              margin-bottom: 3px;
              img {
                width: 85px;
                height: 85px;
                background: none;
              }
              p {
                line-height: 36px;
              }
            }
          }
        }
      }
    }
    .item-list {
      width: 1460px;
      margin: 0 auto;
      .item {
        background: #fff;
        padding: 10px 20px;
        margin-bottom: 20px;
        position: relative;
        .i-title {
          height: 35px;
          span {
            color: #111;
            font-size: 21px;
          }
          a {
            color: #0066c0;
            font-size: 14px;
            margin-left: 20px;
          }
        }
        .i-body {
          height: 220px;
          overflow: hidden;
          .i-list {
            display: flex;
            position: relative;
            left: 0px;
            li {
              text-align: center;
              margin-right: 10px;
              max-height: 200px;
              a {
                display: block;
                min-width: 145px;
                img {
                  display: inline-block;
                  max-height: 200px;
                }
              }
            }
          }
        }
        .prevs,
        .nexts {
          display: none;
          position: absolute;
          top: 50%;
          margin-top: -50px;
          width: 45px;
          height: 100px;
          background: #fff;
          box-shadow: 0 1px 3px #888;
          text-align: center;
          line-height: 100px;
          i {
            font-size: 24px;
          }
        }
        .nexts {
          right: 20px;
        }
      }
    }
  }
}
</style>