<!-- 头部组件 -->
<template>
  <div class="header">
    <div class="h-left">
      <a href="javascript:;" class="category" @click="categoryFn">
        <i class="iconfont icon-fenlei"></i>
      </a>
      <router-link class="logo" to="/">
        <img :src="logo" alt />
      </router-link>
    </div>
    <div :class="flag?'h-center h-center-active':'h-center'">
      <div class="left">
        <span>{{msg}}</span>
        <i class="iconfont icon-webicon215"></i>
        <select v-model="msg">
          <option value="All Departments">All Departments</option>
          <option
            v-for="item in categoryList"
            :key="item.id"
            :value="item.typeName"
          >{{item.typeName}}</option>
        </select>
      </div>
      <div class="center">
        <input type="text" @focus="changetrue" @blur="changefalse" v-model="name" />
      </div>
      <div class="right">
        <button @click="toSearchFn">
          <i class="iconfont icon-sousuo"></i>
        </button>
      </div>
    </div>
    <div class="h-right">
      <div class="national-flag">
        <!-- 国旗小图标 -->
      </div>
      <div class="login">
        <div>Hello!</div>
        <span v-if="userinfo==null">Please log in</span>
        <span v-else>{{userinfo}}</span>
        <i class="iconfont icon-webicon215" v-if="userinfo==null"></i>
        <div class="none" v-if="userinfo==null">
          <!-- 隐藏的登陆按钮 -->
          <button @click="loginFn">Sign-In</button>
        </div>
        <div class="none" v-else>
          <!-- 隐藏的登陆按钮 -->
          <button @click="removeFn">Log out</button>
        </div>
      </div>
    </div>
    <div class="category-msk" @click="mskhideFn">
      <div class="cateList">
        <div class="cate-title" @click.stop>
          <span></span>
          <span v-if="userinfo==null">Hello! Sign-In</span>
          <span v-else>Hello!{{userinfo}}</span>
        </div>
        <div class="cate-main">
          <ul class="cate-ul">
            <li @click.stop>SHOP BY CATEGORY</li>
            <li v-for="item in categoryList" :key="item.id" @click.stop="qiehuan(item.id)">
              <span>{{item.typeName}}</span>
              <i class="iconfont icon-youjiantou1"></i>
            </li>
          </ul>
          <ul class="cate-ul cate-ul-a" @click.stop>
            <li @click="fanhui">
              <h5>
                <i class="iconfont icon-zuojiantou1"></i> MAIN MENU
              </h5>
            </li>
            <li v-for="item in categoryTwoList" :key="item.id" @click="tocategoryFn(item.id)">
              <span>{{item.typeName}}</span>
              <i class="iconfont icon-youjiantou1"></i>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import $ from "jquery";
import Axios from "axios";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      logo: require("../assets/images/logo.png"),
      msg: "All Departments", //搜索导航的分类
      flag: false, //判断搜索框聚焦
      name: "", //搜索框输入的值
      categoryList: [],
      categoryTwoList: [],
      userinfo: this.$cookies.get("username") //用户登陆之后，获取用户名赋予值
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    // userinfo(value) {
    //   this.userinfo = this.$cookies.get("username");
    // }
  },
  //方法集合
  methods: {
    fanhui() {
      //返回到上一级导航
      $(".cate-ul")
        .eq(0)
        .animate(
          {
            left: 0
          },
          200
        );
      $(".cate-ul-a")
        .eq(0)
        .animate(
          {
            left: "365px"
          },
          200
        );
    },
    qiehuan(id) {
      //切换到下一级导航
      for (let key in this.categoryList) {
        if (this.categoryList[key].id == id) {
          this.categoryTwoList = this.categoryList[key].navigation_list;
        }
      }
      $(".cate-ul")
        .eq(0)
        .animate(
          {
            left: "-365px"
          },
          200
        );
      $(".cate-ul-a")
        .eq(0)
        .animate(
          {
            left: "0px"
          },
          200
        );
    },
    changetrue() {
      //input获取焦点时
      this.flag = true;
    },
    changefalse() {
      //input失去焦点时
      this.flag = false;
    },
    loginFn() {
      //跳转到登陆页面
      this.$router.push({
        path: "/login"
      });
    },
    removeFn() {
      //退出登陆
      this.$cookies.remove("username");
      this.userinfo = this.$cookies.get("username");
      this.$message({
        message: "账户退出成功",
        type: "success"
      });
      this.$router.push("/login");
    },
    toSearchFn() {
      //跳转到搜索页面
      if (this.msg == "All Departments") {
        if (this.name) {
          this.$router.push({
            path: `/search?name=${this.name}&flag=3`
          });
        }
      } else {
        let oid;
        for (let key in this.categoryList) {
          if (this.categoryList[key].typeName == this.msg) {
            oid = this.categoryList[key].id;
          }
        }
        //console.log(oid);
        this.$router.push({
          path: `/search?id=${oid}&flag=2&name=${this.name}`
        });
      }
    },
    tocategoryFn(id) {
      //跳转到分类页面
      this.mskhideFn();
      this.fanhui();
      this.$router.push({
        path: "/category?flag=1&cid=" + id
      });
    },
    categoryFn() {
      //弹出分类选项
      $(".category-msk").fadeIn(200); //淡入
      $(".cateList").animate(
        {
          left: 0
        },
        300
      );
      $(".cateList").height(window.innerHeight); //设定分类的高度
      $("#app").height(window.innerHeight); //设定首页的高度
    },
    mskhideFn() {
      //隐藏分类
      $(".cateList").animate(
        {
          left: "-365px"
        },
        200,
        function() {
          $(".category-msk").fadeOut(200);
        }
      );
      $("#app").height("auto");
    },
    getdataList() {
      //请求数据
      Axios.get(this.$store.state.url + "sys/goodsType/navigation").then(
        res => {
          if (res.data.code == 200) {
            this.categoryList = res.data.data;
          }
        }
      );
    }
  },
  mounted() {
    this.getdataList();
    if (this.$cookies.get("username") == null) {
      //判断是否登陆
      this.$message({
        message: "当前未登录，请先登陆",
        type: "warning"
      });
      this.$router.push("/login");
    }
  }
};
</script>
<style lang='less'>
//@import url(); 引入公共css类
.header {
  height: 60px;
  background: #131921;
  display: flex;
  align-items: center;
  .h-left {
    width: 217px;
    height: 60px;
    display: flex;
    .category {
      height: 40px;
      line-height: 40px;
      text-align: center;
      width: 38px;
      margin: 10px 7px 10px 7px;
      border-radius: 3px;
      border: 1px solid #666;
      box-sizing: border-box;
      i {
        font-size: 24px;
        color: #fff;
      }
    }
    .logo {
      width: 165px;
      height: 100%;
      img {
        display: inline-block;
        margin-top: 10px;
        width: 135px;
        height: 36px;
      }
    }
  }
  .h-center {
    min-width: 1300px;
    height: 40px;
    display: flex;
    border-radius: 4px;
    overflow: hidden;
    .left {
      min-width: 80px;
      height: 40px;
      line-height: 40px;
      background: #f3f3f3;
      position: relative;
      text-align: center;
      padding-right: 10px;
      border-right: 1px solid #ccc;
      &:hover {
        background: #dadada;
        span {
          color: #000;
        }
      }
      select {
        width: auto;
        position: absolute;
        display: block;
        top: 0px;
        left: 0;
        min-height: 40px;
        font-family: inherit;
        cursor: pointer;
        opacity: 0;
        line-height: 40px;
        option {
          line-height: 22px;
        }
      }
      span {
        display: inline-block;
        height: 40px;
        line-height: 40px;
        color: #555;
        font-size: 12px;
        padding-left: 10px;
      }
      i {
        font-size: 12px;
        color: #333;
        margin-left: 5px;
      }
    }
    .center {
      flex: 1;
      z-index: 999;
      input {
        width: 100%;
        height: 28px;
        padding: 6px 0;
        outline: none;
        border: none;
        text-indent: 8px;
        font-size: 15px;
      }
    }
    .right {
      button {
        outline: none;
        border: none;
        background: #febd69;
        width: 45px;
        height: 40px;
        cursor: pointer;
        &:hover {
          background: #f80;
        }
        i {
          font-size: 20px;
          font-weight: 700;
        }
      }
    }
  }
  .h-center.h-center-active {
    box-shadow: 0 0 0 2px #f90, 0 0 0 3px rgba(255, 153, 0, 0.5);
  }
  .h-right {
    flex: 1;
    display: flex;
    align-items: center;
    .national-flag {
      display: none;
      width: 24px;
      height: 18px;
      margin-left: 15px;
      margin-top: 5px;
      background: url("../assets/images/guoqi.png") 0px -130px no-repeat;
    }
    .login {
      min-width: 110px;
      height: 50px;
      font-size: 14px;
      cursor: pointer;
      color: #fff;
      margin-left: 30px;
      position: relative;
      &:hover {
        .none {
          display: block;
        }
      }
      div:first-of-type {
        color: #ccc;
        font-size: 12px;
        line-height: 14px;
        height: 14px;
        font-weight: 400;
        margin-top: 9px;
      }
      span {
        font-weight: bold;
      }
      i {
        font-size: 10px;
        color: #ccc;
        margin-left: 5px;
      }
      .none {
        display: none;
        text-align: center;
        width: 200px;
        height: 60px;
        line-height: 60px;
        background: rgba(0, 0, 0, 0.7);
        z-index: 99;
        position: absolute;
        top: 68px;
        left: -45px;
        button {
          width: 150px;
          height: 30px;
          outline: 0;
          border: 1px solid;
          cursor: pointer;
          background: -webkit-linear-gradient(top, #f8e3ad, #eeba37);
          border-color: #c89411 #b0820f #99710d;
          &:hover {
            background: -webkit-linear-gradient(top, #eeba37, #d69f12);
          }
        }
      }
    }
  }
  .category-msk {
    display: none;
    width: 100%;
    height: auto;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    .cateList {
      position: relative;
      left: -365px;
      width: 365px;
      height: 100%;
      background: #fff;
      display: flex;
      flex-direction: column;
      .cate-title {
        height: 50px;
        display: flex;
        align-items: center;
        background: #232f3e;
        span:first-child {
          margin: 0 10px 0 36px;
          width: 27px;
          height: 25px;
          background: url("../assets/images/jingling.png") no-repeat -137px -340px;
        }
        span {
          color: #fff;
          font-size: 19px;
        }
      }
      .cate-main {
        flex: 1;
        overflow-x: hidden;
        position: relative;
        .cate-ul {
          overflow: scroll;
          height: 100%;
          position: relative;
          left: 0;
          li:first-child {
            font-weight: 300;
            color: #969397;
            padding-top: 20px;
          }
          li:last-child {
            padding-bottom: 30px;
          }
          li {
            padding: 13px 20px 13px 36px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;
            line-height: 16px;
            color: #111;
            cursor: pointer;
            i {
              font-size: 14px;
              color: #888;
            }
          }
        }
        .cate-ul.cate-ul-a {
          width: 365px;
          position: absolute;
          left: 365px;
          top: 0;
        }
      }
    }
  }
}
</style>