<!-- 分类页面搜索页面 -->
<template>
  <div class="category">
    <Header ref="headers" />
    <div class="category-main">
      <div class="category-left">
        <div class="c-title">
          <span>Commodity classification</span>
        </div>
        <ul class="u-list">
          <li>{{leftCategory.typeName}}</li>
          <li
            v-for="item in leftCategory.navigation_list"
            :key="item.id"
            @click="getproductFn(1,item.id)"
          >{{item.typeName}}</li>
        </ul>
      </div>
      <div class="category-right">
        <p
          class="p-title"
          v-if="productList.length>0"
        >Prices and other details may vary depending on size and color</p>
        <div v-if="productList.length>0">
          <ul class="right-list" :style="{'min-height':ProductHeight+'px'}">
            <li v-for="item in productList" :key="item.id">
              <router-link :to="'/detail?id='+item.id">
                <div>
                  <img :src="item.goodsMainImgPath" alt />
                </div>
                <p>{{item.goodsName}}</p>
              </router-link>
            </li>
          </ul>
          <div class="pagination" v-if="total>16">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="total"
              :page-size="16"
              :current-page="current"
              @current-change="catecurrentFn"
            ></el-pagination>
          </div>
        </div>
        <div v-else class="noProduct" :style="{'min-height':ProductHeight+'px'}">暂无数据</div>
      </div>
    </div>
    <Footer ref="footers" />
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import Header from "../components/Header";
import Footer from "../components/footer";
import $ from "jquery";
import Axios from "axios";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    Header,
    Footer
  },
  data() {
    //这里存放数据
    return {
      productList: [],
      ProductHeight: 0,
      leftCategory: {},
      total: 0, //数据总条数，用于分页
      current: 1, //当前页数
      cid: 0
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    $route(to) {
      this.getproductFn(1, to.query.cid);
      this.getLeftCateFn(to.query.flag, to.query.cid);
    },
    total() {
      this.current = 1;
    }
  },
  //方法集合
  methods: {
    getproductFn(current, id) {
      Axios.get(
        this.$store.state.url +
          `sys/goods/navigation/goods?current=${current}&size=16&id=${id}`
      ).then(res => {
        //console.log(res);
        if (res.data.code == 200) {
          this.cid = id;
          this.total = res.data.total;
          this.productList = res.data.data;
          this.ProductHeight =
            window.innerHeight -
            this.$refs.headers.$el.offsetHeight - //减去header高度
            this.$refs.footers.$el.offsetHeight - //减去footer高度
            65; //减去内边距
        }
      });
    },
    getLeftCateFn(flag, id) {
      Axios.get(
        this.$store.state.url + `sys/goods/navigation?flag=${flag}&id=${id}`
      ).then(res => {
        //console.log(res);
        if (res.data.code == 200) {
          this.leftCategory = res.data.data.navigation;
        }
      });
    },
    catecurrentFn(current) {
      this.current = current;
      this.getproductFn(current, this.cid);
    }
  },
  mounted() {
    $("#app").height("auto");
    this.getproductFn(1, this.$route.query.cid);
    this.getLeftCateFn(this.$route.query.flag, this.$route.query.cid);
  }
};
</script>
<style lang='less'>
.category-main {
  width: 1500px;
  height: auto;
  margin: 0 auto;
  display: flex;
  padding-top: 15px;
  .category-left {
    border-right: 2px solid #ddd;
    width: 272px;
    margin-right: 12px;
    .c-title {
      margin-bottom: 10px;
      font-size: 13px;
      line-height: 15px;
      color: #111;
      font-weight: bold;
    }
    .u-list {
      font-size: 13px;
      li:first-child {
        margin-left: 14px;
        font-weight: bold;
        &:hover {
          color: #111;
        }
      }
      li {
        margin: 0px 0px 6px 24px;
        cursor: pointer;
        &:hover {
          color: #c45500;
        }
      }
    }
  }
  .category-right {
    flex: 1;
    .p-title {
      height: 50px;
      font-size: 13px;
      line-height: 50px;
      padding-left: 12px;
    }
    .right-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        width: 276px;
        padding: 0 12px;
        a {
          display: block;
          border-bottom: 2px solid #ddd;
          margin-bottom: 26px;
          height: 410px;
          div {
            height: 275px;
            display: flex;
            align-items: center;
            justify-content: center;
            img {
              max-width: 275px;
              max-height: 275px;
            }
          }
          p {
            font-size: 15px;
            line-height: 22px;
            height: 88px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
            color: #111;
            margin-top: 10px;
            &:hover {
              color: #c45500;
            }
          }
        }
      }
    }
    .pagination {
      text-align: center;
      line-height: 60px;
    }
    .right-list::after {
      content: "";
      flex: auto;
    }
    .noProduct {
      font-size: 30px;
      color: #ccc;
      text-align: center;
      padding-top: 50px;
      height: auto;
    }
  }
}
</style>