<!-- 注册页面 -->
<template>
  <div class="register">
    <div class="register-logo">
      <router-link to="/" tag="span"></router-link>
    </div>
    <div class="register-main">
      <div class="main-box">
        <h1>Create account</h1>
        <div class="input">
          <p>Your name</p>
          <input type="text" v-model="username" />
        </div>
        <div class="input">
          <p>pet name</p>
          <input type="text" v-model="realname" />
        </div>
        <div class="input">
          <p>Password</p>
          <input type="password" placeholder="At least 6 characters" v-model="password" />
          <div class="password">
            <i></i>
            <span>Passwords must be at least 6 characters.</span>
          </div>
        </div>
        <div class="input">
          <p>Re-enter password</p>
          <input type="password" v-model="passwords" />
        </div>
        <div class="input">
          <p>Gender</p>
          <div class="sex">
            <input type="radio" name="sex" value="1" v-model="sex" />
            <span>Men</span>
            <input type="radio" name="sex" value="2" v-model="sex" />
            <span>Woman</span>
          </div>
        </div>
        <div class="input">
          <p>cell-phone number</p>
          <input type="text" v-model="Phone" />
        </div>
        <div class="submit">
          <button @click="registerFn">Create your Amazon account</button>
        </div>
      </div>
    </div>
    <div class="register-footer"></div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
import Axios from "axios";
import qs from "qs"; //将数据序列化
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      password: "", //密码
      passwords: "", //确认密码
      username: "", //用户名
      realname: "", //昵称
      Phone: "", //手机号
      sex: "" //性别
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    toregisterFn() {
      //跳转注册页面
      this.$router.push({
        path: "/register"
      });
    },
    registerFn() {
      //点击注册
      let obj = {
        username: this.username, //用户名
        password: this.password, //密码
        realname: this.realname, //昵称
        sex: this.sex, //性别
        Phone: this.Phone //手机号
      };
      for (let key in obj) {
        if (obj[key] == "") {
          this.$message.error("请将信息完整");
          return;
        }
      }
      let phone = /^[1][35678][0-9]{9}$/; //验证手机号正则
      if (obj.password != this.passwords) {
        this.$message.error("两次输入密码不一致");
        return;
      } else if (!phone.test(obj.Phone)) {
        this.$message.error("手机号格式不正确");
        return;
      }
      Axios.post(
        this.$store.state.url + "sys/user/registe",
        qs.stringify(obj)
      ).then(res => {
        console.log(res);
        if (res.data.code == 200) {
          this.$message({
            message: "注册成功",
            type: "success"
          });
          this.$router.push("/");
        } else if (res.data.code == 400) {
          this.$message.error("该用户名已存在");
        }
      });
    }
  }
};
</script>
<style lang='less'>
//@import url(); 引入公共css类
