      display: inline-block;
      width: 103px;
      height: 31px;
      background: url("../assets/images/jingling2.png") no-repeat -5px -130px;
      background-size: 400px 750px;
      cursor: pointer;
    }
  }
  .register-main {
    width: 700px;
    margin: 10px auto 35px;
    .main-box {
      margin: 0 auto 22px;
      width: 298px;
      padding: 20px 26px;
      border-radius: 4px;
      border: 1px solid #ccc;
      h1 {
        font-weight: 400;
        font-size: 28px;
        line-height: 1.2;
        margin-bottom: 10px;
      }
      .input {
        margin-bottom: 10px;
        p {
          padding-left: 2px;
          padding-bottom: 2px;
          font-weight: 700;
          font-size: 13px;
          line-height: 19px;
          color: #111;
          font-family: "Hiragino Sans GB", "Microsoft Yahei", Arial, sans-serif;
        }
        & > input {
          width: 100%;
          background-color: #fff;
          height: 31px;
          padding: 3px 7px;
          box-sizing: border-box;
          line-height: normal;
          border: 1px solid #a6a6a6;
          border-radius: 3px;
          outline-color: #f90;
        }
        .password {
          display: flex;
          height: 20px;
          align-items: center;
          span {
            font-size: 12px;
            line-height: 15px;
            color: #2b2b2b;
          }
          i {
            height: 13px;
            width: 14px;
            background: url("../assets/images/jingling2.png") no-repeat -166px -130px;
          }
        }
        .sex {
          font-size: 14px;
          color: #2b2b2b;
          height: 30px;
          display: flex;
          align-items: center;
          input {
            width: 16px;
            height: 16px;
          }
          span {
            margin-right: 10px;
          }
        }
      }
      .submit {
        padding-top: 10px;
        button {
          width: 100%;
