          &:hover {
            background: #f0c14b;
          }
        }
      }
    }
  }
  .register-footer {
    position: relative;
    text-align: center;
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.14),
      rgba(0, 0, 0, 0.03) 3px,
      transparent
    );
    &::after {
      display: block;
      width: 100%;
      height: 50px;
      line-height: 50px;
      font-size: 11px;
      color: #555;
      background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0), #fff);
      z-index: 1;
      content: "Copyright 2020 jinghuiyue company";
    }
  }
}
</style>