<!--首页 -->
<template>
  <div class="index">
    <Header />
    <!-- banner图 -->
    <div class="banner-wrap">
      <div class="banner">
        <ul class="banner-img" id="banner-img">
          <li v-for="item in bannerList" :key="item.id">
            <img :src="item.imagePath" />
          </li>
        </ul>
        <div class="next" @click="nextFn"></div>
        <div class="prev" @click="prevFn"></div>
      </div>
    </div>
    <Main />
    <Footer />
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
import Header from "../components/Header";
import Footer from "../components/footer";
// import Axios from "axios";
import $ from "jquery";
import Main from "./component/indexMain";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    Header,
    Main,
    Footer
  },
  data() {
    //这里存放数据
    return {
      bannerList: [
        {
          id: 1,
          imagePath: require("../assets/images/banner1.jpg")
        },
        {
          id: 2,
          imagePath: require("../assets/images/banner2.jpg")
        },
        {
          id: 3,
          imagePath: require("../assets/images/banner3.jpg")
        },
        {
          id: 4,
          imagePath: require("../assets/images/banner4.jpg")
        },
        {
          id: 5,
          imagePath: require("../assets/images/banner5.jpg")
        },
        {
          id: 6,
          imagePath: require("../assets/images/banner6.jpg")
        },
        {
          id: 7,
          imagePath: require("../assets/images/banner7.jpg")
        }
      ],
      current: 0
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    nextFn() {
      //点击向右滚动轮播图
      $("#banner-img").animate(
        {
          left: "-1500px"
        },
        500,
        function() {
          $("#banner-img li")
            .eq(0)
            .appendTo($("#banner-img"));
          $("#banner-img").css("left", 0);
        }
      );
    },
    prevFn() {
      //点击向左滚动轮播图
      $("#banner-img li:last").prependTo($("#banner-img"));
      $("#banner-img").css("left", "-1500px");
      $("#banner-img").animate({
        left: "0"
      });
    }
  },
  mounted() {}
};
</script>
<style lang='less'>
.index {
  background: #eaeded;
  position: relative;
}
.banner-wrap {
  max-height: 250px;
  .banner {
    width: 1500px;
    min-height: 600px;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
    &::after {
      //图片底部虚化
      left: 0;
      pointer-events: none;
      position: absolute;
      right: 0;
      top: 250px;
      z-index: 1;
      background: linear-gradient(-180deg, rgba(234, 237, 237, 0), #eaeded);
      bottom: 0;
      content: "";
    }
    .banner-img {
      display: flex;
      position: relative;
      left: 0px;
    }
    .next,
    .prev {
      position: absolute;
      top: 0;
      width: 80px;
      height: 250px;
      cursor: pointer;
    }
    .next {
      right: 0;
      background: url("../assets/images/bannerclick.png") -24px center no-repeat;
      background-size: 68px 52px;
    }
    .prev {
      left: 0;
      background: url("../assets/images/bannerclick.png") 37px center no-repeat;
      background-size: 68px 52px;
    }
  }
}
</style>